<template>
<div id="login">
  账号: <input type="text" v-model="loginForm.username">
  密码: <input type="text" v-model="loginForm.password">
  <button @click="submit">登录</button>
</div>
</template>

<script>
import  {mapMutations} from 'vuex';
export default {
  name: "login",
  data(){
    return{
      loginForm: {
        username: '',
        password: ''
      },
      usertoken: ''
    }
  },
  methods:{
    ...mapMutations(['changeLogin','changeUserGroup','changeMeetingRooms']),
    submit(){
      let _this = this;
      if (this.loginForm.username === '' || this.loginForm.password === '') {
        alert('账号或密码不能为空');
      } else{
        let data = JSON.stringify(_this.loginForm)
        console.log(data)
        this.axios({
          method: 'post',
          url:'/user/login',
          data: data
        }).then(res =>{
          console.log(res.data)
          if (res.data.state === "true") {
            _this.userToken = res.data.token;
            // 将用户token保存到vuex中
            _this.changeLogin ({Authorization: _this.userToken});
            _this.changeUserGroup( {userGroup : res.data.userGroup} )
            _this.axios.get('/meetingroom/getAll')
              .then((res)=>{
                this.changeMeetingRooms(res.data.meetingRooms)
              })
              .catch((error)=>{
                console.log (error)
                this.$message.error('获取会议室错误')
              })
            const redirect = _this.$route.query.redirect
            if (redirect) {
              this.$router.replace ({
                path: redirect
              })
            } else {
              this.$router.replace ({
                path: "/home"
              })
            }
            _this.$message.success ('登录成功')
          }else{
            this.$message.error(res.data.msg);
          }
        }).catch(error=>{
          this.$message.error('未知错误，回调落入了catch,请联系管理员')
          console.log(error)
        })
      }
    }
  }
}
</script>

