<template>
  <div id="upload1">
    <el-table
        border
        height="65vh"
        stripe
        :data="meetings"
        style="width: 100%">
      <el-table-column
          prop="roomName"
          label="会议室"
      >
      </el-table-column>
      <el-table-column
          prop="location"
          label="地点">
      </el-table-column>
      <el-table-column
          prop="floor"
          label="楼层">
      </el-table-column>
      <el-table-column
          prop="plateNum"
          label="门牌号">
      </el-table-column>
      <el-table-column
          label="文件">
        <template slot-scope="scope">
            <div slot="reference" class="name-wrapper">
              <el-tag size="medium">{{ scope.row.fileName }}</el-tag>
            </div>
          <div class="upload" @click="uploadF">
          <img src="../static/add.png" alt="">
          </div>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
export default {
  name:'notice',
  data() {
    return {
      meetings:[]
    }
  },
  methods:{
    uploadF:function (){
      return 0
    }
  },
  created() {
    this.axios.get('/meetingroom/getAll')
        .then(res=>{
          console.log (res.data)
          this.meetings = res.data.meetingRooms
          for (let i = 0;i<this.meetings.length ;i++){
            this.meetings[i].fileName = '暂无文件'
          }
        })
        .catch(error=>{
          this.$message.error(error + ',发生未知错误,请联系管理员')
        })
  }
}
</script>

<style scoped>
#upload1 .name-wrapper{
  display: inline-block;
}
.upload{
  display: inline-block;
  margin-left: 0.5vw;
  position: relative;
  top: 0.8vh;
}
.upload:hover{
  box-shadow: skyblue 0 0 10px;
  transition: 0.5s;
}
.upload img{
  display: inline-block;
  width: 20px;
  height: 20px;
}
</style>

<style>
.el-tag--medium{
  display: inline-block !important;
}
.cell{
  font-weight: bolder !important;
  color: black !important;
  text-align: center !important;
}
.el-table{
  width: 80% !important;
  margin-left: 10vw;
  margin-top: 5vh;
  box-shadow: 0 0 10px skyblue;
}
.is-leaf,
.gutter{
  background: #FAFAFA !important;
}

.el-table .cell {
  white-space: pre-line !important;
}
</style>